<template>
  <div class="page" v-nav="{title: '注册详情', showBackButton: true, onBackButtonClick: back}">
    <scroller ref="scroller" class="page-content gaoe-detail no-scrolling">
    <!-- <div class="page-content gaoe-detail"> -->
      <div class="dl-title" :class="{ hide : head_tips }" v-text="data.head_tips"></div>
      <div class="company-info" v-show="!data.type">
        <div class="cpy-title" @click="$router.forward('/gaoe/background/' + taskId)">
          <span class="cpy-logo" v-if="data.company && data.company.icon"><img class="cpy-logo-img" :src="data.company.icon"/></span>
