        <div class="detail-text" v-for="item in data.steps">
          <span class="text-title" v-text="item.title"></span>
          <span class="text-content" v-text="item.desc"></span>
        </div>
        <div class="godaddy" @click="showGaoeGuide()">
          <span class="g-icons" :style="{ width : ( tutorial.length + 1 ) * 86 + 'px' }">
