            </span>
            <span class="g-icon" v-for="(t, index) in tutorial" :style="{
              backgroundImage: 'url(' + t.pic + '!150x150)',
              backgroundSize: '100% 100%',
              backgroundPosition: 'center',
              backgroundRepeat: 'no-repeat'
            }"><i class="g-icon-index" v-text="index + 1"></i></span>
          </span>
        </div>
        <a class="btn start-btn" href="javascript:void(0);" target="_blank" @click="startTask()">开始任务</a>
      </div>

      <div class="submit-task" v-if="data.is_idfa_task">
        <div class="submit-bx">
          <div style="display: inline-block;width: 100%;">
            <a class="btn submit-btn"  v-on:click="submitIDFA($event)">提交任务</a>
          </div>
        </div>
        <div class="submit-bx"><p class="task-text-prompt" v-text="data.tail_tips">任务提交后到账需要1～3个工作日</p></div>
      </div>

      <div class="submit-task" v-if="!data.is_idfa_task">
        <div class="submit-info-md">
          <label class="name">姓名</label>
          <input class="ipt name-ipt" v-model="form.username" type="text" placeholder="注册所用姓名"/>
        </div>
        <div class="submit-info-md">
          <label class="name">手机号</label>
          <input class="ipt tel-ipt" type="tel" v-model="form.mobile" maxlength="11" placeholder="注册所用手机号"/>
          <a class="code" :class="{ disabled: isdisabled }" v-text="getCodeText" v-on:click="sendCode()"></a>
        </div>
        <div class="submit-info-md">
          <label class="name">验证码</label>
          <input class="ipt code-ipt" type="tel" v-model="form.code" maxlength="4" placeholder="收到的验证码"/>
        </div>
        <div class="submit-bx"><a class="btn submit-btn"  v-on:click="submit($event)">提交任务</a></div>
        <div class="submit-bx"><p class="task-text-prompt" v-text="data.tail_tips">任务提交后到账需要1～3个工作日</p></div>
      </div>

      <!-- dummy -->
      <div style="height: 50px; background: #fff;"></div>
    <!-- </div> -->
    </scroller>
  </div>
