<script>
  /* eslint eqeqeq: "off", camelcase: "off" */

  import CountDown from '../widget/CountDown'
  import GaoeGuide from './GaoeGuide'
  import channel from './channel'

  import {
    popup_abandon_gaoe,
    popup_submit_check,
    popup_gaoe_weixin_task,
    popup_choose_verify_code_type
  } from './popups.js'

  // const task_completed_code = 40007
  const return_to_task_code = 40008

  export default {
    components: {
      CountDown
    },
    data () {
      return {
        taskId: 0,
        getCodeLock: false,
        submitLock: false,
        getCodeText: '获取验证码',
        isdisabled: false,
        canSubmit: false,
        code_timer: 0,
        tagState: false,
        textState: false,
        head_tips: false,
        form: {
          username: '',
          mobile: '',
          code: ''
        },
        data: {},
        deadline: 0,
        tutorial: [], // 图文教程
        voiceVerify: false
      }
    },

    mounted () {
      this.taskId = this.$route.params.id

      if (!this.taskId) {
        $router.back('/gaoe/')
        return
      }

      $api.gaoeDetail(this.taskId)
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          if (payload.status == 1) { // 非进行中任务，请先领取任务
            $router.back('/gaoe/')
            return
          }

          this.data = payload
          this.tutorial = payload.tutorial
          this.deadline = payload.deadline * 1000
          payload.company.tags.length > 0 ? this.tagState = false : this.tagState = true // 判断tags是否为空
          payload.need_to_know === '' ? this.textState = true : this.textState = false // 判断need_to_know是否为空
          payload.head_tips === '' ? this.head_tips = true : this.head_tips = false // 判断head_tips是否为空
          let tag = document.querySelector('.godaddy')
          tag.addEventListener('touchmove', function (event) {
            event.stopPropagation()
          })
          tag.addEventListener('touchend', function (event) {
            event.stopPropagation()
          })

          // 高额任务教程
          $modal.fromComponent(GaoeGuide).then((modal) => {
            this.gaoeGuideModal = modal

            channel.$on('closeModal', () => {
              modal.hide()
            })
          })

          setTimeout(this.$refs.scroller.resize, 0)
        })
    },

    destroyed () {
      if (this.gaoeGuideModal) $modal.destroy(this.gaoeGuideModal)
      this.stop()
    },

    methods: {
      startTask () {
        if (this.data.type == 'weixin_task') {
          let popup = $popup.fromTemplate(
            popup_gaoe_weixin_task.template(this.data.task_image, this.data.title),
            popup_gaoe_weixin_task.options
          )
          popup.show()
        } else {
          location.href = this.data.official_url
        }
      },
      back () {
        if (this.taskId) {
          let popup = $popup.fromTemplate(
            popup_abandon_gaoe.template,
            popup_abandon_gaoe.options
          )

          popup.show().then(buttonIndex => {
            if (buttonIndex === 0) {
              $api.gaoeGiveup(this.taskId).then(payload => {
                $router.back('/gaoe')
              })
            }
          })
        }
      },

      stop () {
        if (this.code_timer) clearInterval(this.code_timer)
      },

      resetCounterBtn () {
        this.getCodeLock = false
        this.getCodeText = '获取验证码'
        this.isdisabled = false
      },

      sendCode () {
        if (this.voiceVerify) {
          this.popCodeType()
        } else {
          this.code(1)
        }
      },

      code (type) {
        if (this.getCodeLock) {
          return false
        }
        this.getCodeLock = true

        let phone = this.form.mobile
        let s = 60
        let tips = ''

        if (!phone) {
          tips = '手机号不能为空'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        } else {
          this.code_timer = setInterval(() => {
            s--
            if (s === 0) {
              this.resetCounterBtn()
              this.stop()
            } else {
              this.getCodeLock = true
              this.isdisabled = true
              this.getCodeText = '已发送' + '(' + s + 's' + ')'
            }
          }, 1000)
        }

        if (tips) {
          $toast.show(tips)
          setTimeout(() => { this.getCodeLock = false }, 500)
          return
        }
        this.voiceVerify = true
        $api.highearnDetailSendCode({phone: phone, task_id: this.taskId, type: type})
        .then(res => res.data)
        .then((data) => {
          if (data.err_code) {
            $toast.show(data.err_msg)
            this.resetCounterBtn()
            this.stop()

            if (data.err_code === return_to_task_code) {
              setTimeout(() => {
                $router.forward('/gaoe')
              }, 1000)
            }

            return
          }
        },

        () => {
          this.resetCounterBtn()
          this.stop()
          return
        }
      )
      },

      popCodeType () {
        let popup = $popup.fromTemplate(
            popup_choose_verify_code_type.template,
            popup_choose_verify_code_type.options
          )
        popup.show().then((index) => {
          if (index === 0) {
            this.code(1)
          }
          if (index === 1) {
            this.code(2)
          }
        })
      },

      submitIDFA (event) {
        if (this.submitLock) {
          return false
        }
        this.submitLock = true

        this.form.task_id = this.taskId

        $api
          .hiearnCallbackReward(this.form)
          .then((res) => {
            this.submitLock = false
            let data = res.data

            if (data.err_code) {
              $toast.show(data.err_msg)
              setTimeout(() => {
                $router.forward('/gaoe')
              }, 1000)
              return
            }

            let payload = data.payload
            let msg = payload.message

            if (msg.indexOf('成功提交') != -1) {
              let template = (message) => {
                return `
                  <div class="desc text-center">${message}</div>
                `
              }
              $popup
                .fromTemplate(template(payload.wait_apply_words), {
                  effect: 'scale',
                  cssClass: 'popup-general',
                  buttons: [{ text: '知道了' }]
                })
                .show()
                .then(btn => {
                  $router.forward('/gaoe')
                })
            } else if (msg.indexOf('回调已经成功') != -1) {
              // console.log('do nothing!')
            } else {
              // 其他的错误信息
              setTimeout(() => {
                let template = (message) => {
                  return `
                    <div class="desc text-center">${message}</div>
                  `
                }
                $popup
                  .fromTemplate(template(msg), {
                    effect: 'scale',
                    cssClass: 'popup-general',
                    buttons: [{ text: '知道了' }]
                  })
                  .show()
                  .then(btn => {
                    $router.forward('/gaoe')
                  })
              }, 1000)
            }
          },
          () => {
            this.submitLock = false
          })
      },

      submit (event) {
        if (this.submitLock) {
          return false
        }
        this.submitLock = true

        let name = this.form.username
        let phone = this.form.mobile
        let code = this.form.code
        let tips = ''
        let valid = false
        let regx = new RegExp('\\s')

        if (this.data.deadline <= 0) {
          tips = '任务已过时'
        } else if (!name) {
          tips = '请输入姓名'
        } else if (regx.test(name)) {
          tips = '姓名格式不正确'
        } else if (!phone) {
          tips = '请输入手机号'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        } else if (!code) {
          tips = '请输入验证码'
        } else if (!/^[\d]{4}$/.test(code)) {
          tips = '验证码格式不正确'
        } else {
          valid = true
        }

        if (tips) {
          $toast.show(tips)
          setTimeout(() => { this.submitLock = false }, 500)
          if (!valid) {
            return
          }
        }
        this.postSubmitData()
      },

      postSubmitData () {
        this.form.task_id = this.taskId
        $api.hiearnApplyForReward(this.form)
          .then((res) => {
            this.submitLock = false
            let data = res.data
            if (data.err_code) {
              $toast.show(data.err_msg)
              if (data.err_code === return_to_task_code) {
                setTimeout(() => {
                  $router.forward('/gaoe')
                }, 1000)
              }

              return
            }

            let popup = $popup.fromTemplate(
              popup_submit_check.template(data.payload.wait_apply_words),
              popup_submit_check.options
            )
            popup.show().then((buttonIndex) => {
              if (buttonIndex == 0) {
                $router.forward('/gaoe')
              }
            })
          },
          () => {
            this.submitLock = false
          })
      },

      showGaoeGuide () {
        this.gaoeGuideModal.show()
        channel.$emit('setGaoeGuideSlides', this.tutorial)
      }
    }
  }
</script>



