<template>
  <div class="page-content">
    <div class="bar bar-header" style="background: transparent; box-shadow: none; padding: 0 5px;">
      <h1 class="title light" v-text="title" style="margin-top: 6px;"></h1>
      <div class="button-back" @click="closeModal()"></div>
    </div>

    <swiper v-if="slides.length > 0"
      ref="swiper"
      direction="horizontal"
      hide-pager="true"
      :callback="callback"
    >
      <swiper-item v-for="(s, index) in slides" :key="index">
        <div class="slide">
          <p v-text="s.desc"></p>
          <img :src="s.pic" alt="">
        </div>
      </swiper-item>
    </swiper>
    <i class="left-icon" :class="{ 'hide': current === 0 }" v-on:click="prev()"></i>
    <i class="right-icon" :class="{ 'hide': current === slides.length - 1 }" v-on:click="next()"></i>
  </div>
</template>
<style lang="scss" scoped>
  .page-content {
    /* background-color: rgba(0,0,0,0.80); */
    background-color: #2f2f2f;
    background-image: url(//qianka.b0.upaiyun.com/images/1d0965adcaf48d64b57ebbc4cd20ca9d.png);
    background-size: 100% 64px;
    background-repeat: no-repeat;
  }

  .button-back {
    position: absolute;
    top: 16px;
    right: 10px;
    width: 25px;
    height: 25px;
    z-index: 100px;
    background-image: url(//qianka.b0.upaiyun.com/images/24c28298410595e40dd7cd3caa9b2fbb.png);
    background-size: 13px 13px;
    background-repeat: no-repeat;
    background-position: center;
  }

  .ion-ios-close-empty::before {
    margin: 0px;
    padding: 0px;
    width: 24px;
    height: 32px;
    font-size: 24px;
    margin-top: -16px;
  }

  .slide {
    img {
      position: absolute;
      left: 75px;
      top: 68px - 44px;
      width: 225px;
      height: 400px;
      z-index: 2;
    }

    p {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 115px;
      font-size: 14px;
      line-height: 20px;
      text-align: justify;
      color: #fff;
      background-image: linear-gradient(-180deg, rgba(0, 0, 0, 0.01) 0%, #000000 100%);
      z-index: 1;
      margin-bottom: 0;
      padding: 48px 16px 27px 16px;
    }
  }

  .left-icon {
    position: absolute;
    display: block;
    top: 259px;
    left: 15px;
    width: 12px;
    height: 21px;
    background: url("//qianka.b0.upaiyun.com/images/6839dfd2bc9351365da0f04e350eb479.png") no-repeat;
    background-size: 100% 100%;
    z-index: 2;
  }

  .right-icon {
    position: absolute;
    display: block;
    top: 259px;
    right: 15px;
    width: 12px;
    height: 21px;
    background: url("//qianka.b0.upaiyun.com/images/19050021ab0535e666c0e67c5f4eeaad.png") no-repeat;
    background-size: 100% 100%;
    z-index: 2;
  }

  .left-icon, .right-icon {
    transition: opacity 0.15s;
    -webkit-transition: opacity 0.15s;
    opacity: 1;
  }

  .hide {
    opacity: 0;
  }
</style>
<script>
  import channel from './channel'

  export default {
    data () {
      return {
        slides: [],
        title: '',
        current: 0
      }
    },

    mounted () {
      channel.$on('setGaoeGuideSlides', (slides) => {
        this.slides = slides
        setTimeout(() => {
          let activeIndex = this.$refs.swiper.activeIndex
          this.title = (activeIndex + 1) + '/' + this.slides.length
        }, 0)
      })
    },

    methods: {
      callback (prev, current) {
        this.title = current + 1 + '/' + this.slides.length
        this.current = current
      },

      prev () {
        this.$refs.swiper.prev()
      },

      next () {
        this.$refs.swiper.next()
      },

      closeModal () {
        channel.$emit('closeModal')
      }
    }
  }
</script>



