/* eslint eqeqeq: "off", camelcase: "off" */
require('./popups.scss')

// 任务须知弹层
const popup_must_know = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '放弃'},
      {text: '继续（s）'}
    ]
  },
  template: (desc) => {
    return `
      <div class="title">任务须知</div>
      <div class="desc">${desc}</div>
    `
  }
}

// 匹配失败弹层
const popup_compare_fail = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },
  template: `
    <p class="title">很抱歉</p>
    <p class="desc">您的情况不满足该广告商要求，无法领取该任务</p>
  `
}

// 温馨提示
const popup_pleaseant_remind = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '放弃'},
      {text: '继续'}
    ]
  },
  template: `
    <p class="title">温馨提示</p>
    <p class="desc">您已完成过本任务，再次完成需要提供不同的身份资料（父母、伴侣、朋友）</p>
  `
}

// 是否放弃任务
const popup_abandon_gaoe = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '放弃'},
      {text: '不放弃'}
    ]
  },

  template: `
    <div class="desc">是否放弃任务</div>
  `
}

// 任务已提交审核
const popup_submit_check = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: (wait_apply_words) => {
    let words = wait_apply_words || '预计1～3个工作日内奖励到账'
    return `
      <div class="title">任务已提交审核</div>
      <div class="desc">${words}</div>
    `
  }
}

// 申诉失败 给testpopup用来测试申诉的
// TODO: 结束后和css一起删掉
const popup_task_fail = {
  options: {
    effect: 'scale',
    cssClass: 'popup-task-appeal',
    buttons: [
      {text: '知道了'}
    ]
  },
  template: (taskname) => {
    return `
      <div class="icon"><img src="//qianka.b0.upaiyun.com/images/30613d5771c0ec9a7651b8fdac41318b.png"></div>
      <div class="title">申诉失败</div>
      <div class="desc">试玩任务《${taskname}》奖励未到账申诉失败，理由：提交资料不足以证明完成本任务</div>
    `
  }
}

// 放弃进行中的任务, 抢新的任务
const popup_snatch_new_task = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '取消'},
      {text: '确认'}
    ]
  },

  template: `
    <div class="desc">不能同时抢多个任务哦!<br>要放弃上个任务并接取该任务吗?</div>
  `
}

// 任务完成
const popup_task_success = {
  options: {
    effect: 'scale',
    cssClass: 'popup-task-success',
    buttons: [
      {text: '知道了', theme: 'assertive'}
    ]
  },
  template: (options) => {
    let amount = options.amount || '0.0'
    let taskType = options.taskType || '试玩任务'
    let taskName = options.taskName || ' '

    return `
      <div class="logo"><img src="//qianka.b0.upaiyun.com/images/416b9d3b792ea8a3484db8dc5a137299.png"></div>
      <scalable class="reward" height="62">
        <span class="rmb">+</span>
        <span class="amount">${parseFloat(amount)}</span>
        <span class="yuan">元</span>
      </scalable>
      <div class="task-info">
        <div class="line"></div>
        <div class="task-type">${taskType}</div>
        <div class="task-name">${taskName}</div>
      </div>
    `
  }
}

// 冻结账号提示
const popup_user_freeze = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: () => {
    return `
      <div class="title">很抱歉</div>
      <div class="desc">
        您的账号已被冻结，解冻请
        <a class="assertive" href="/v4/help/complainUnfreeze" >前往申诉</a>
      </div>
    `
  }
}

// 特殊微信任务 点击开始任务弹层
const popup_gaoe_weixin_task = {
  options: {
    effect: 'scale',
    cssClass: 'popup-gaoe-weixin-task popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template (imgUrl, taskName) {
    return `
      <div class="task-title mb-4">用微信搜索</div>
      <div class="task-title mb-20">并关注“${taskName}”后完成任务</div>
      <img class="weixin-guid-img" src="${imgUrl}" alert=""/>
    `
  }
}

// 验证码类型提示
const popup_choose_verify_code_type = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '短信验证码' },
      { text: '语音验证码' }
    ],
    showClose: true
  },
  template: `
    <div class="title">没收到验证码？</div>
    <div class="desc">
      您可以选择获取语音验证码
    </div>
  `
}

module.exports = {
  popup_must_know,
  popup_compare_fail,
  popup_pleaseant_remind,
  popup_abandon_gaoe,
  popup_submit_check,
  popup_task_fail,
  popup_snatch_new_task,
  popup_task_success,
  popup_user_freeze,
  popup_gaoe_weixin_task,
  popup_choose_verify_code_type
}



